@import (less) "color.less";
.dividing-line-container {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    background-color: transparent;
}

.dividing-line-left-continuous {
    flex-grow: 1;
    align-self: flex-start;
    padding-left: 20px;
}

.dividing-line-left {
    .dividing-line-left-continuous;
    padding-right: 20px;
}

.dividing-line-right-continuous {
    flex-grow: 1;
    align-self: flex-start;
    padding-right: 20px;
}

.dividing-line-right {
    .dividing-line-right-continuous;
    padding-left: 20px;
}

hr.line-green {
    border: 0.5px solid @green;
}

hr.line-gray {
    border: 0.5px solid @light_gray;
}

.dividing-line-content {
    align-self: center;
}

.dividing-line-title-icon {
    font-size: 2em;
    color: @gray;
    vertical-align: middle;
    margin-right: 10px;
}

.dividing-line-title {
    font-size: 1.125em;
    color: @darkest_gray;
    vertical-align: middle;
}

.dividing-line-button {
    align-self: center;
    padding-right: 20px;
    .dividing-line-button-icon {
        vertical-align: middle;
        font-size: 1.875em;
        cursor: pointer;
        color: @green;
        &.hover-icon{
            display: none;
        }
    }
    .dividing-line-button-label {
        vertical-align: middle;
        color: @green;
        font-size: 0.750em;
        font-weight: 700;
        text-transform: uppercase;
    }
    &:hover{
        .dividing-line-button-icon{
            display: none;
        }
        
        .hover-icon{
            display: inline !important;
        }
    }
}

.show-button {
    height: 40px;
}


:host-context(.contrasto-on) {

    .dividing-line-title-icon {
        color: @black;
    }
    
    .dividing-line-title {
        color: @black;
    }
    
    .dividing-line-button {
        .dividing-line-button-icon {
            color: @black !important;
            // &:hover::before {
            //   background-color: @black;
            //   color: @white;
            // }
        }
        .dividing-line-button-label {
            color: @black !important;
        }
    }

    hr.line-green, hr.line-gray {
        border: 0.5px solid @black;
    }

}